<template>
    <div id="cheditor">
        <navigation class="nav"/>
        <div class="editor-box area">
            <input id="title" v-model="articleTitle" placeholder="请输入标题" placeholder-style="text-align: center"/>
            <div id="toolbar-container"></div>
            <div id="editor"></div>
            <div id="active-bar">
                <div class="article-label">
                    <span class="title">文章标签:</span>
                    <alInput @submit="selectedLabel"/>
                </div>
                <div class="article-type">
                    <span class="title">文章类型:</span>
                    <el-radio v-model="articleType" label="原创">原创</el-radio>
                    <el-radio v-model="articleType" label="转载">转载</el-radio>
                    <el-radio v-model="articleType" label="翻译">翻译</el-radio>
                </div>
                <div class="article-form">
                    <span class="title">发布形式:</span>
                    <el-radio v-model="articleWay" label="0">公开</el-radio>
                    <el-radio v-model="articleWay" label="1">私密</el-radio>
                </div>
                <div class="article-sumbit">
                    <el-button size="medium" @click="submit(1)">发布</el-button>
                    <el-button size="medium" @click="submit(0)">保存为草稿</el-button>
                    <el-button size="medium" @click="backtrack">返回</el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import navigation from "@/components/navigation_bar";
    import CKEditor from '@ckeditor/ckeditor5-build-decoupled-document';
    import "@ckeditor/ckeditor5-build-decoupled-document/build/translations/zh-cn.js";
    import alInput from "./article_lable_input";
    import api from "@/config/api";
    import req from "@/utils/req";
    import uploadUtil from "./uploadUtil";
    import UploadAdapter from "@/utils/enery/CKEditorUploadAdapter";

    export default {
        data() {
            return {
                articleId: null, // 文章ID
                articleTitle: null, // 文章标题
                articleType: null, // 文章类型
                articleWay: null, // 发布形式
                articleLable: null, // 文章标签
                editor: null, // 编辑器实例
                submitType: null // 提交类型
            }
        },
        created() {
            this.$router.currentRoute.meta.keepAlive = true;
        },
        mounted() {
            this.initCKEditor();
        },
        methods: {

            /**
             * 初始化富文本编辑器
             */
            initCKEditor() {
                CKEditor.create(document.querySelector('#editor'),{
                    language: 'zh-cn'
                }).then(editor => {
                    const toolbarContainer = document.querySelector('#toolbar-container');
                    toolbarContainer.appendChild(editor.ui.view.toolbar.element);
                    this.editor = editor;
                    editor.plugins.get('FileRepository').createUploadAdapter = (loader) => {
                        return new UploadAdapter(loader);
                    };
                }).catch(error => {
                    this.$notify({
                        title: "错误",
                        message: error,
                        duration: 0
                    })
                });
            },

            /**
             * 选中文章标签
             */
            selectedLabel(label) {
                this.articleLable = label;
            },

            /**
             * 检测文章是否合格
             */
            checkArticle() {
                let message;
                if (!this.articleTitle) {
                    message = "您还没有输入文章标题";
                }
                else if (!this.editor.getData()) {
                    message = "您的内容为空";
                }
                else if (!this.articleLable) {
                    message = "请为您的文章输入一个标签吧";
                }
                else if (!this.articleType) {
                    message = "请选择文章类型";
                }
                else if (!this.articleWay) {
                    message = "请选择提交方式";
                }
                if (message) {
                    this.$notify({
                        title: "温馨提示",
                        message: message
                    });
                    return false;
                }
                return true;
            },

            /**
             * 发布
             */
            submit(type) {
                this.submitType = type;
                if(!this.checkArticle()) return;
                uploadUtil.clearImgIds(this.editor.getData());
                if (this.articleId) {
                    this.updateBlog();
                } else {
                    this.release();
                }
            },

            /**
             * 发布博客
             */
            release() {
                req(api.release, {
                    title: this.articleTitle,
                    content: this.editor.getData(),
                    label: this.articleLable,
                    type: this.articleType,
                    way: this.articleWay,
                    state: this.submitType
                }).then(res => {
                    this.submitLast(res);
                });
            },

            /**
             * 更新博客
             */
            updateBlog() {
                req(api.updateArticle, {
                    id: this.articleId,
                    title: this.articleTitle,
                    content: this.editor.getData(),
                    label: this.articleLable,
                    type: this.articleType,
                    way: this.articleWay,
                    state: this.submitType,
                }).then(res => {
                    this.submitLast(res);
                })
            },

            /**
             * 处理博客发布结果
             */
            submitLast(res) {
                let self = this;
                if (res.code == 1000) {
                    this.articleId = res.data;
                    this.$confirm(this.submitType == 1 ? "你的博客发布成功啦！赶快去看看吧！" : "你的博客保存成功", "温馨提示", {
                        confirmButtonText: "去看看", cancelButtonText: "我还想再改改", type: "success"}).then(res => {
                        this.$router.currentRoute.meta.keepAlive = false;
                        self.$router.replace({name: "blogDetail", query:{id: self.articleId}});
                    }).catch(res => {
                        return;
                    });
                } else {
                    this.$notify({
                        title: "警告",
                        message: res.msg,
                        type: "warning",
                        duration: 0
                    });
                }
            },

            /**
             * 页面关闭时删除缓存中的id
             */
            async beforeunloadFn(e) {
            },

            /**
             * 回退
             */
            backtrack() {
                this.$router.go(-1);
            }
        },
        components: {
            navigation: navigation,
            alInput: alInput
        },
        beforeRouteEnter(to, from, next) {
            window.document.body.classList.add("cheditor-background");
            next();
        },
        beforeRouteLeave(to, from, next) {
            window.document.body.classList.remove("cheditor-background");
            next();
        }
    }
</script>

<style lang="scss">
    .cheditor-background {
        @include background("../../assets/img/b3.jpg", $width, $repeat);
    }
    #cheditor {
        opacity: 1;
        width: 100%;
        @include align(24, $column);
    }
    #cheditor {
        /** 编辑框 **/
        .editor-box {
            margin-bottom: 3rem;
            width: $areaWidth;
            max-height: 1300px;
            display: flex;
            flex-flow: column nowrap;
        }
        #toolbar-container, #editor, #title {
            width: 100%;
        }
        /** 工具栏 **/
        #toolbar-container {
            margin-top: 1rem;
        }
        /** 标题栏 **/
        #title {
            border: none;
            font-size: 1rem;
            text-align: center;
            background-color: #EFEFEF;
            @include sizing(10px);
        }
        #title:focus {
            outline: none;
        }

        /** 操作栏 **/
        #active-bar {
            margin: 3rem 0 1rem;
            @include font(0.8rem);
        }
        #active-bar > div {
            margin-top: 1rem;
        }
        #active-bar .title {
            margin-right: 1rem;
        }

        /** 富文本 **/
        #editor {
            min-height: 90vh;
            border: 1px solid #C4C4C4;
            overflow-y: scroll;
            @include sizing(10px);
        }
    }
</style>
