import req from "@/utils/req";
import api from "@/config/api";
import ul from "@/utils/url";
import uploadUtil from "@/view/cheditor/uploadUtil";

/**
 * CKEditor5 上传适配器
 */
export default class CKEditorUploadAdapter {
    constructor(loader) {
        this.loader = loader;
    }

    /**
     * 上传
     */
    async upload() {
        let param = new FormData();
        param.append("file", await this.loader.file);
        param.append("allowSize", "10");
        return req(api.uploadFile, param).then(res => {
            uploadUtil.storeImgId(res.data);
            return Promise.resolve({default: process.env.VUE_APP_BASE_URL + ul.assignment(api.getImg.uri, {id: res.data})});
        });
    }

    /**
     * 退出
     */
    abort() {
        console.info("执行 退出");
    }
}
