import sto from "@/utils/sto";
import req from "@/utils/req";
import api from "@/config/api";
import axios from 'axios';
import ul from "@/utils/url";

export default {
    /**
     * 缓存图片ID
     */
    storeImgId(id) {
        let imgIds = sto.getStore("imgIds") || "";
        imgIds += imgIds ? "," + id : id;
        sto.setStore("imgIds", imgIds);
    },

    /**
     * 清楚多余上传的图片
     */
    clearImgIds(content) {
        let storeImgIds = sto.getStore("imgIds");
        if (!storeImgIds) return;
        let imgIds = storeImgIds.split(",");
        let superfluous = [];
        imgIds.forEach((id,index) => {
            if (content.indexOf(id) == -1) {
                superfluous.push(id);
            }
        });
        if (superfluous.length > 0) {
            axios.delete(api.deleteFileList.uri + ul.arrConversionGetParam("id", superfluous)).then(res => {
                sto.setStore("imgIds", "");
            });
        }
    }
}
